import React from 'react'
import ImgAbout from '../assets/imagen11.jpg';
import '../styles/About.css';
import { Link } from 'react-router-dom';

function About() {
  return (
    <div className='about'>
      <div className='topabout' style={{backgroundImage: `url(${ImgAbout})`}}></div>
      <div className='bottomabout'>
        <h1>FELIPE GARCS A</h1>
        <h2>Piloto TC2000 Colombia</h2>
        <p>Felipe Garcs es un ingeniero de produccin radicado en la ciudad de Medelln Colombia, desde pequeo acompaaba siempre a su padre a diferentes competencias automovilsticas a nivel local, de tipo amateur o aficionado, como diferentes eventos de trepadores o circuitos urbanos. Es por esta razon, que desde siempre existi el interes por el automovilismo.</p>
        <p>En el ao 2020 en medio de la pandemia por COVID-19 que sacudi al mundo entero, uno de los planes de entretenimiento fue el simulador de competencias, a travs del cual se revivi la pasin por el automovilismo.</p>
        <p>Es por esto que durante el ao 2020 y en plena pandemia se inici con la construccin del actual vehculo de competencia, para hacer el debut en el campeonato TC2000 en la categora Academa Clase A en la tercera competencia del ao 2021, finalizando su participacin en el 2021 en la categora TC Junior.</p>
        <p>Para el ao 2022 se realizan grandes mejoras en la plataforma del vehculo, primero pasando el cilindraje a 2.000cc y cambiando componentes por unos de mejor calidad, as iniciara la temporada 2022 en la categora TC Junior.</p>
        <Link to = '/contacto'>
            <button>CONTACTO</button>
        </Link>
      
      </div>
    </div>
  )
}

export default About